
#app {
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  color: white;
  font-size: 1.3vw;
  overflow-x: hidden;
}
.header-right .ivu-badge .ivu-icon {
  width: 1vw;
}
.header-right .ivu-badge .ivu-badge-count {
    height: 1vw;
    width: 1vw;
    border-radius: 50%;
    line-height: 1vw;
    min-width: 1vw;
    padding: 0;
    font-size: 1vw;
    top: 40%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
