    position: fixed;
    width: 100vw;
}
.layout-content.ivu-layout {
  margin-top: 50px;
}
.ivu-dropdown-item {
  z-index: 3;
}
.left-close {
  margin-left: 64px;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
.left-open {
  margin-left: 165px;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
@media screen and ( max-width: 768px ) {
#headerIcon {
    display: none;
}
.left-close {
    margin-left: 0px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
.left-open {
    margin-left: 0px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
}

.header {
    height: inherit;
}
.header-left {
    float: left;
}
.header-right {
    /* width: 420px; */
    height: inherit;
    text-align: right;
    margin-right: 20px;
}
.count {
    top: 10px;
    left: 10px
}
.header-right .ivu-col:hover {
    background: blue;
}
.header-right .ivu-menu {
    color: white;
}
.header-right .ivu-menu-submenu-title span {
    display: inline-block;
}
.header-right .collapsed-menu span{
    width: 0 !important;
    height: 0;
    overflow: hidden;
}
.header-right .collapsed-menu .ivu-icon {
    font-size: 30px;
}
.header-right .collapsed-menu .ivu-menu {
    display: none;
}
.header-right .collapsed-menu .ivu-tooltip {
    position: fixed;
}

.menu-item span {
  display: inline-block;
  overflow: hidden;
  width: 69px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
  -webkit-transition: width 0.2s ease 0.2s;
  transition: width 0.2s ease 0.2s;
}
.sider-left .collapsed-menu {
  margin-left: -15px;
}
.collapsed-menu span {
  width: 0px;
  -webkit-transition: width 0.2s ease;
  transition: width 0.2s ease;
}
.collapsed-menu i {
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
  -webkit-transition: font-size 0.2s ease 0.2s, -webkit-transform 0.2s ease 0.2s;
  transition: font-size 0.2s ease 0.2s, -webkit-transform 0.2s ease 0.2s;
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s, -webkit-transform 0.2s ease 0.2s;
  vertical-align: middle;
  font-size: 22px;
}
.collapsed-menu .ivu-menu-submenu-title-icon {
  display: none;
}
.collapsed-menu .ivu-menu {
  display: none;
}
.sider-left .ivu-menu {
  color: white;
}

